// 因為Next已經包含react，不需另外import

class Prices extends React.Component {
  state = {
    currency: "USD",
  };
  render() {
    return (
      <div>
        <ul className="list-group">
          <li>{this.props.bpi}</li>
        </ul>
      </div>
    );
  }
}

export default Prices;
